.DieZweiteLuftSlide {
    .image {
        transform: scale(1.38);
        .logo {
            background-image: url(./logos/spotify.png);
            opacity: .15;
            

            @media screen and (max-width: 1020px) {
                transform: scale(2.8);
            }

            @media screen and (max-width: 580px) {
                transform: scale(1.2);
            }
        }
    }

}
